<template>
  <form
      v-bind:accept-charset="acceptCharset"
      v-bind:action="action"
      v-bind:data-values="dataValues"
      v-bind:method="method"
      v-bind:enctype="enctype"
      @submit="handleSubmit"
      @ajax:beforeSend="ajaxBeforeSend"
      ref="form"
  >
    <slot></slot>
  </form>
</template>

<script setup>
import { toRefs, computed, ref, watch } from "vue";
import { useStore } from "vuex"
const props = defineProps({
  acceptCharset: {
    type: String,
    required: true,
    default: 'UTF-8'
  },
  action: {
    type: String,
    required: true,
  },
  enctype: {
    type: String,
  },
  method: {
    type: String,
    required: true,
    default: "post"
  },
  autoSubmit: {
    type: String,
    default: ''
  }
})
const timer = ref(null)
const form = ref(null)
const { acceptCharset, action, enctype, method, autoSubmit } = toRefs(props)
const store = useStore();
const values = computed(() => {
  return store.getters.getValue(store.getters.getMeta('modelName'))
})
const debounce = (timeout = 500) => {
  clearTimeout(timer.value)
  // GOTCHA: DO NOT USE form.submit() because it does not play well with HOTWIRE
  //  * instead, use form.requestSubmit() that acts as if a submit button were clicked
  //    and therefor is compatible with HOTWIRE
  timer.value = setTimeout(() => {
    form.requestSubmit();
  }, timeout)
}
const handleSubmit = (event) => {
  if (store.getters.getMeta('disableValidation')) {
    return // early exit (will continue with the submit)
  }
  store.commit('setTouched', {
    value: true,
    name: `${store.getters.getMeta('modelName')}._submit`
  })
  if (!store.getters.getError(`${store.getters.getMeta('modelName')}._is_valid`)) {
    // invalid data => du not submit
    event.preventDefault();
  }
}
const ajaxBeforeSend = (event) => {
  if (!store.getters.getError(`${store.getters.getMeta('modelName')}._is_valid`)) {
    // ignore RailsUjs: make that when using rails ujs with remote:true (aka local:false) we do not send the form
    // if the data is valid note: there is no test for this specific case !!
    event.preventDefault();
  }
}
watch(values, (value, _oldValue) => {
  if (autoSubmit.value === "onChange") {
    debounce(750)
  }
});
</script>
