<template>
  <ComponentWrapper :name="name" :classes="inputGroupClass" :id="id">

    <input v-bind="$attrs"
           :class="inputClass"
           ref="telInput"
           type="tel"
           :id="id"
           :value="inputValue"
           @input="inputValue=$event.target.value"
           @focus="inputTouched=true"
           :name="name"/>

    <span :id="id + '__error_message'" v-if="displayValidationError" class="tel-input__error-feedback">{{ inputError }}</span>
    <span :id="id + '__warning_message'" v-if="displayValidationWarning" class="input-block__warning-feedback">{{ inputWarning }}</span>

  </ComponentWrapper>
</template>


<script setup>
import { toRefs, computed, ref, onMounted } from "vue";
import { useStore } from "vuex";
import 'intl-tel-input/build/css/intlTelInput.css';
import intlTelInput from "intl-tel-input";
import ComponentWrapper from "./component_wrapper.vue";

defineOptions({
  inheritAttrs: false
})

const props = defineProps({
  name: {
    type: String,
    required: true,
  },
  id: {
    type: String,
    required: true
  }
})
const telInput = ref(null);
const store = useStore();
const { name, id } = toRefs(props);
// Computed methods
const displayValidationError = computed(() => {
  return inputTouched.value && inputError.value;
})
const displayValidationWarning = computed(() => {
  return inputTouched.value && !inputError.value && inputWarning.value;
})
const inputClass = computed(() => {
  return {
    "input-block__field": true,
    "tel-input__text-input": true,
    "input-block__field--invalid": (inputTouched.value && inputError.value),
  }
})
const inputGroupClass = computed(() => {
  return {
    "input-block tel-input-block": true,
  }
})
const inputError = computed(() => {
  if (inputTouched.value) {
    return store.getters.getError(name.value)
  } else {
    return null;
  }
})
const inputWarning = computed(() => {
  if (inputTouched.value) {
    return store.getters.getWarning(name.value)
  } else {
    return null;
  }
})
const inputTouched = computed({
  get () {
    return store.getters.getTouched(name.value);
  },
  set (value) {
    store.commit('setTouched', {
      value: value,
      name: name.value
    });
  }
});
const inputValue = computed({
  get () {
    return store.getters.getValue(name.value);
  },
  set (value) {
    let iti = window.intlTelInputGlobals.getInstance(telInput.value);
    value = iti.getNumber();
    store.dispatch('update', {
      value: value,
      name: name.value
    });
  }
});
onMounted(() => {
  intlTelInput(telInput.value, {
    preferredCountries: [],
    formatOnDisplay: false,
    separateDialCode: false,
    // fixme: should be the path to intl-tel-input/build/js/utils but I dont know how to get it while using vite js
    utilsScript: "https://cdn.jsdelivr.net/npm/intl-tel-input@17.0.21/build/js/utils.js"
  });
})
</script>
