<template>
  <ComponentWrapper :name="name" :classes="inputGroupClass" :id="id">
    <div v-if="prepend" class="input-block__prepend" :id="id + '__prepend'">
      <span v-html="prepend"></span>
    </div>

    <input type="hidden" :name="name" :value="inputValue" @input="inputValue = $event.target.value"/>

    <flat-pickr
        ref="input"
        v-bind="$attrs"
        v-model="inputFormattedValue"
        :id="id"
        :class="[
            inputClass,
            prepend ? 'input--has-prepend' : '',
             append ? 'input--has-append' : '',
             ]"
        :config="config"
        @on-open="onFocusDatePicker"
    ></flat-pickr>

    <div v-if="append" class="input-block__append" :id="id + '__append'">
      <span v-html="append"></span>
    </div>

    <ErrorsPlaceholder v-if='displayError' :names=[name]></ErrorsPlaceholder>
  </ComponentWrapper>
</template>


<script setup>
import { computed, ref, toRefs, watch } from "vue";
import { useStore } from "vuex";
import { debounce } from "lodash";
import { formatISO, isMatch, isValid, parse } from "date-fns";
import { format, utcToZonedTime, zonedTimeToUtc } from "date-fns-tz";
import flatPickr from "vue-flatpickr-component";
import ConfirmDatePlugin from 'flatpickr/dist/plugins/confirmDate/confirmDate';
import 'flatpickr/dist/plugins/confirmDate/confirmDate.css';
import "flatpickr/dist/flatpickr.css";
import * as R from 'ramda'
import ErrorsPlaceholder from "./errors_placeholder.vue";
import ComponentWrapper from "./component_wrapper.vue";

defineOptions({
  inheritAttrs: false,
})

const props = defineProps({
  append: {
    type: String,
  },
  name: {
    type: String,
    required: true,
  },
  id: {
    type: String,
    required: true,
  },
  prepend: {
    type: String,
  },
  suggest_value: { // do not use directly (instead use the computed property suggestValue)
    type: [String, Boolean],
    required: false,
    default: false
  },
  display_error: { // do not use directly (instead use the computed property displayError)
    type: [String, Boolean],
    required: false,
    default: true
  },
  display_format: {
    type: String,
    required: false,
    default: "dd/MM/yyyy HH:mm",
  },
  time_zone: {
    type: String,
    required: true,
  }
})

const store = useStore();
const { append, name, id, prepend, suggest_value, display_error, display_format, time_zone } = toRefs(props);

// Methods
const onFocusDatePicker = () => {
  inputTouched.value = true
}

const pickerFormat = () => {
  let format;

  if (display_format.value === 'dd MMM yy HH:mm') {
    format = 'j M y H:i'
  } else if (display_format.value === 'dd/MM/yyyy HH:mm') {
    format = 'd/m/Y H:i';
  } else {
    format = 'd/m/Y H:i';
  }

  return format
}

const setFormattedValue = (value) => {
  let displayFormat = display_format.value

  if (!R.isNil(value) && !R.isEmpty(value) && !isMatch(value, displayFormat)) {
    // ignore non-blank & unmatched values
    // user is probably typing a new date, wait for him to finish
    return
  }

  let date = parse(value, displayFormat, getCurrentDateInTimezone());
  let normalised_date = isValid(date) ? formatISO(zonedTimeToUtc(date, time_zone.value)) : ''

  store.dispatch("update", {
    value: normalised_date,
    name: name.value,
  });
}


const getCurrentDateInTimezone = () => {
  const zeroPad = (num, places) => {
    let zero = places - num.toString().length + 1;
    return Array(+(zero > 0 && zero)).join("0") + num;
  }

  // construct the date in UTC (ex: 2014-06-25T10:00:00.000Z)
  let date = new Date()
  let year = zeroPad(date.getUTCFullYear(), 4)
  let month = zeroPad(date.getUTCMonth() + 1, 2)
  let day = zeroPad(date.getUTCDate(), 2)
  let hour = zeroPad(date.getUTCHours(), 2)
  let minute = zeroPad(date.getUTCMinutes(), 2)
  let second = zeroPad(date.getUTCSeconds(), 2)
  let dateString = `${year}-${month}-${day}T${hour}:${minute}:${second}.000Z`

  // convert to timezone
  return utcToZonedTime(dateString, time_zone.value)
}


const config = ref({
  dateFormat: pickerFormat(),
  allowInput: true,
  enableTime: true,
  time_24hr: true,
  "plugins": [new ConfirmDatePlugin({ confirmText: "Done" })]
})

const debouncedSetFormattedValue = debounce(setFormattedValue, 500)


// Computed
const displayValidationError = computed(() => {
  return inputTouched.value && inputError.value;
})


const displayValidationWarning = computed(() => {
  return inputTouched.value && !inputError.value && inputWarning.value;
})

const suggestValue = computed(() => {
  return (suggest_value.value === 'true' || suggest_value.value === true || suggest_value.value === "force")
})

const displayError = computed(() => {
  return (display_error.value === 'true' || display_error.value === true)
})

const useSuggestedValue = computed(() => {
  return (suggestValue.value && !inputTouched.value) || suggest_value.value === 'force'
})

const suggestedValue = computed(() => {
  return store.getters.getSuggestedValues(name.value);
})

const inputClass = computed(() => {
  return {
    "input-block__field": true,
    "input-block__field--invalid": (inputTouched.value && inputError.value),
    "input-block__field--warning": (inputTouched.value && !inputError.value && inputWarning.value),
    datepicker: true,
  };
})


const inputError = computed(() => {
  if (inputTouched.value) {
    return store.getters.getError(name.value);
  } else {
    return null;
  }
})

const inputWarning = computed(() => {
  if (inputTouched.value) {
    return store.getters.getWarning(name.value);
  } else {
    return null;
  }
})

const inputGroupClass = computed(() => {
  return {
    "input-block": true,
  };
})

const inputTouched = computed({
  get () {
    return store.getters.getTouched(name.value);
  },
  set (value) {
    store.commit('setTouched', {
      value: value,
      name: name.value
    });
  }
});

const inputValue = computed({
  get () {
    return store.getters.getValue(name.value);
  },
  set (value) {
    store.dispatch("update", {
      value: value,
      name: name.value,
    });
  }
});


const inputFormattedValue = computed({
  get () {
    let value = store.getters.getValue(name.value)
    let tempDate = value == null ? "" : new Date(value);

    if (isValid(tempDate)) {
      // it is safe to parse Value using utcToZonedTime
      return format(
          utcToZonedTime(value, time_zone.value),
          display_format.value
      );
    } else {
      return "";
    }
  },
  set (value) {
    // user is probably typing a new date, wait for him to finish
    // and avoid unnecessary updates of the state
    debouncedSetFormattedValue(value)
  }
});

watch(suggestedValue, (value, _oldValue) => {
  if (useSuggestedValue.value) {
    inputValue.value = value;
  }
});
</script>
