<template>
  <div :class="[inputGroupClass, classes]" :id="id + '__wrapper'">
    <slot></slot>
  </div>
</template>

<script setup>
import { computed } from 'vue';
import { useStore } from "vuex";
const store = useStore();
const props = defineProps({
  name: {
    type: String,
    required: true
  },
  id: {
    type: String,
    required: true
  },
  classes: {
    type: [String, Array, Object],
    default: ''
  }
})
const isEmpty = computed(() => {
  const value = store.getters.getValue(props.name);
  return value === undefined || value === null || value === '';
});
const inputGroupClass = computed(() => {
  return {
    "botyglot-form--class-when-not-empty": !isEmpty.value,
    "botyglot-form--class-when-empty": isEmpty.value
  };
});
</script>
