<template>
  <ComponentWrapper :name="name" :classes="inputGroupClass" :id="id">
    <input type="hidden" :name="name" :value="inputValue" @input="inputValue=$event.target.value" />

    <VueMonacoEditor
        v-bind="$attrs"
        ref="editor"
        :id="id"
        :class="inputClass"
        language="ruby"
        v-model:value="inputValue"
        @focus="inputTouched = true"
        :options="MONACO_EDITOR_OPTIONS"
        @editorDidMount="onEditorDidMount"
    />

    <ErrorsPlaceholder v-if='displayError' :names=[name]></ErrorsPlaceholder>
  </ComponentWrapper>
</template>


<script setup>
import { toRefs, computed, watch } from "vue";
import { useStore } from "vuex";
import { VueMonacoEditor } from '@guolao/vue-monaco-editor'

import ComponentWrapper from "./component_wrapper.vue";
import ErrorsPlaceholder from "./errors_placeholder.vue";

defineOptions({
  inheritAttrs: false,
})

const props = defineProps({
  id: {
    type: String,
    required: true
  },
  name: {
    type: String,
    required: true
  },
  readonly: {
    type: Boolean,
    required: false,
    default: false
  },
  suggest_value: { // do not use directly (instead use the computed property suggestValue)
    type: [String, Boolean],
    required: false,
    default: false
  },
  display_error: { // do not use directly (instead use the computed property displayError)
    type: [String, Boolean],
    required: false,
    default: true
  },
})

const store = useStore();
const { id, name, readonly, suggest_value, display_error } = toRefs(props)


const displayValidationError = computed(() => inputTouched.value && inputError.value);

const displayValidationWarning = computed(() => inputTouched.value && !inputError.value && inputWarning.value);

const suggestValue = computed(() => suggest_value.value === "true" || suggest_value.value === true || suggest_value.value === "force");

const displayError = computed(() => display_error.value === "true" || display_error.value === true);

const useSuggestedValue = computed(() => (suggestValue.value && !inputTouched.value) || suggest_value.value === 'force');

const suggestedValue = computed(() => store.getters.getSuggestedValues(name.value));

const inputClass = computed(() => {
  return {
    "input-block__field": true,
    "input-block__field--invalid": (inputTouched.value && inputError.value),
    "input-block__field--warning": (inputTouched.value && !inputError.value && inputWarning.value),
    "editor": true
  }
});

const inputError = computed(() => {
  if (inputTouched.value) {
    return store.getters.getError(name.value);
  } else {
    return null;
  }
});

const inputWarning = computed(() => {
  if (inputTouched.value) {
    return store.getters.getWarning(name.value);
  } else {
    return null;
  }
});

const inputGroupClass = computed(() => {
  return {
    "input-block": true,
  }
});

const inputTouched = computed({
  get () {
    return store.getters.getTouched(name.value);
  },
  set (value) {
    store.commit('setTouched', {
      value: value,
      name: name.value
    });
  }
});

const inputValue = computed({
  get () {
    return store.getters.getValue(name.value);
  },
  set (value) {
    store.dispatch('update', {
      value: value,
      name: name.value
    });

    inputTouched.value = true
  }
});

const MONACO_EDITOR_OPTIONS = {
  minimap: { enabled: false },
  automaticLayout: true,
  autoIndent: true,
  lineNumbers: 'on',
  readOnly: readonly.value,
  autoClosingBrackets: 'always',
  formatOnPaste: true,
  formatOnType: true,
  tabSize: 2
}

const onEditorDidMount = (editor) => {
  let count = 5;
  let countdownInteval = setInterval(() => {
    editor.layout();
    count--
    if (count === 0) {
      clearInterval(countdownInteval);
    }
  }, 100);
};

watch(suggestedValue, (value, _oldValue) => {
  if (useSuggestedValue.value) {
    inputValue.value = value;
  }
});
</script>
