<template>
  <div class="form-validation">
    <input v-bind="$attrs" @click="submitting" :disabled="disabled"/>

    <div class="form-validation__global-error"
         v-if="displayValidationWarning"
         @click="displayValidationMessages = !displayValidationMessages">!
    </div>

    <ul class="form-validation__tooltip" v-if="displayValidationWarning & displayValidationMessages">
      <li v-for="error in recapErrors" :key="error">{{ error }}</li>
    </ul>
  </div>
</template>


<script setup>
import { computed, ref } from "vue";
import { useStore } from "vuex";
defineOptions({
  inheritAttrs: false,
})
const store = useStore();
const displayValidationWarning = computed(() => {
  return inputTouched.value
      && !store.getters.getMeta(`hideValidationErrors`)
      && !store.getters.getError(`${store.getters.getMeta('modelName')}._is_valid`)
})
const inputTouched = computed({
  get () {
    return store.getters.getTouched('modelName')
  },
  set (value) {
    store.commit('setTouched', {
      value: value,
      name: name.value
    })
  }
});
const recapErrors = computed(() => {
  let recap = store.getters.getError(store.getters.getMeta("modelName"));
  delete recap._is_valid;
  return Object.values(recap);
})
const disabled = ref(false)
const displayValidationMessages = ref(false)
const submitting = () => {
  if (store.getters.getError(`${store.getters.getMeta('modelName')}._is_valid`)) {
    setTimeout(() => disabled.value = true);
  }
}
</script>
