<template>
  <span v-if="displayValidationMessage" :class="inputClass"> {{ inputMessage }}  </span>
</template>


<script setup>
import { computed, toRefs } from 'vue';
import { useStore } from "vuex";
const props = defineProps({
  names: { // do not use directly (instead use the computed property computed_names)
    type: [String, Array],
    required: true
  },
  id: {
    type: String,
  },
  force: {
    type: [String, Boolean],
    default: false,
    require: false
  }
})
const store = useStore();
const { names, force } = toRefs(props);
const computed_names = computed(() => {
  if (typeof names.value === 'string') {
    return JSON.parse(names.value);
  } else {
    return names.value;
  }
});
const inputTouched = computed({
  get () {
    if (force === true || force === "true") return true;

    let result = null;
    computed_names.value.forEach(name => {
      let touched = store.getters.getTouched(name);
      if (touched && touched !== null && touched !== false) {
        result = touched;
      }
    });
    return result;
  },
  set (value) {
    computed_names.value.forEach(name => {
      store.commit('setTouched', {
        value: value,
        name: name
      });
    });
  }
});
const inputError = computed(() => {
  if (inputTouched.value) {
    let result = "";
    computed_names.value.forEach(name => {
      let error = store.getters.getError(name);
      if (error && error !== "" && result === "") {
        result = error;
      }
    });
    return result;
  } else {
    return null;
  }
});
const inputWarning = computed(() => {
  if (inputTouched.value) {
    let result = "";
    computed_names.value.forEach(name => {
      let warning = store.getters.getWarning(name);
      if (warning && warning !== "" && result === "") {
        result = warning;
      }
    });
    return result;
  } else {
    return null;
  }
});
const displayValidationError = computed(() => inputTouched.value && inputError.value);

const displayValidationWarning = computed(() => inputTouched.value && !inputError.value && inputWarning.value);

const displayValidationMessage = computed(() => displayValidationError.value || displayValidationWarning.value);

const inputClass = computed(() => ({
  "input-block__error-feedback": displayValidationError.value,
  "input-block__warning-feedback": displayValidationWarning.value
}));

const inputMessage = computed(() => inputError.value || inputWarning.value);
</script>
