<template>
  <ComponentWrapper :name="name" :classes="inputGroupClass" :id="id">

    <label :class="inputClass" :for='id'>
      <span class="toggle-label-prepend" v-if="showLabels">{{ off_label }}</span>
      <input type="hidden" :name="name" :value="off_value"/>

      <input :id="id"
             :name="name"
             class="toggle_switch_component__input"
             type='checkbox'
             ref="input"
             v-bind="$attrs"
             :disabled="disabled"
             :checked="inputValue === on_value"
             @focus="inputTouched=true"
             @keydown.space.prevent="toggleInputValue"
             @click="toggleInputValue"
             :value="on_value"
      />

      <span class="toggle_switch_component__display" hidden></span>
      <span class="toggle-label-append" v-if="showLabels">{{ on_label }}</span>
      <p v-if="!showLabels">{{ inputLabel }}</p>
    </label>

    <ErrorsPlaceholder v-if='displayError' :names=[name]></ErrorsPlaceholder>
  </ComponentWrapper>
</template>


<script setup>
import { computed, toRefs, watch } from "vue";
import { useStore } from "vuex";

import ErrorsPlaceholder from "./errors_placeholder.vue";
import ComponentWrapper from "./component_wrapper.vue";

const props = defineProps({
  name: {
    type: String,
    required: true
  },
  id: {
    type: String,
    required: true
  },
  value: {
    type: String,
    required: true
  },
  on_value: {
    type: String,
    required: true
  },
  on_label: {
    type: String,
    required: true
  },
  off_value: {
    type: String,
    required: true
  },
  off_label: {
    type: String,
    required: true
  },
  disabled: {
    type: Boolean,
    required: false
  },
  suggest_value: { // do not use directly (instead use the computed property suggestValue)
    type: [String, Boolean],
    required: false,
    default: false
  },
  display_error: { // do not use directly (instead use the computed property displayError)
    type: [String, Boolean],
    required: false,
    default: true
  },
  show_labels: {
    type: [String, Boolean],
    default: false
  }
})

const store = useStore();
const {
  name,
  id,
  value,
  on_value,
  on_label,
  off_value,
  off_label,
  disabled,
  suggest_value,
  display_error,
  show_labels
} = toRefs(props);

const toggleInputValue = () => {
  if (inputValue.value === on_value.value) {
    inputValue.value = off_value.value;
  } else {
    inputValue.value = on_value.value;
  }
}

const displayValidationError = computed(() => {
  return inputTouched.value && inputError.value;
})

const displayValidationWarning = computed(() => {
  return inputTouched.value && !inputError.value && inputWarning.value;
})

const showLabels = computed(() => {
  return (show_labels.value === 'true' || show_labels.value === true)
})

const suggestValue = computed(() => {
  return (suggest_value.value === 'true' || suggest_value.value === true || suggest_value.value === 'force');
})

const displayError = computed(() => {
  return (display_error.value === 'true' || display_error.value === true)
})

const useSuggestedValue = computed(() => {
  return (suggestValue.value && !inputTouched.value) || suggest_value.value === 'force'
})

const suggestedValue = computed(() => {
  return store.getters.getSuggestedValues(name.value)
})

const inputClass = computed(() => {
  return {
    "toggle_switch_component__label": true,
    "input-block__field--invalid": (inputTouched.value && inputError.value),
    "input-block__field--warning": (inputTouched.value && !inputError.value && inputWarning.value)
  }
})

const inputGroupClass = computed(() => {
  return {
    "toggle_switch_component__global-container": true,
  }
})

const inputValue = computed({
  get () {
    let value = store.getters.getValue(name.value)
    if (value === true || value === on_value.value) {
      return on_value.value;
    } else {
      return off_value.value;
    }
  },
  set (value) {
    store.dispatch('update', {
      value: (value === true || value === on_value.value) ? on_value.value : off_value.value,
      name: name.value
    })
  }
});

const inputLabel = computed({
  get () {
    let value = store.getters.getValue(name.value)
    if (value === true || value === on_value.value) {
      return on_label.value
    } else {
      return off_label.value
    }
  }
});

const inputError = computed(() => {
  if (inputTouched.value) {
    return store.getters.getError(name.value)
  } else {
    return null
  }
})

const inputWarning = computed(() => {
  if (inputTouched.value) {
    return store.getters.getWarning(name.value)
  } else {
    return null
  }
})

const inputTouched = computed({
  get () {
    return store.getters.getTouched(name.value)
  },
  set (value) {
    store.commit('setTouched', {
      value: value,
      name: name.value
    });
  }
});

watch(suggestedValue, (value, _oldValue) => {
  if (useSuggestedValue.value) {
    inputValue.value = value;
  }
});
</script>
