<template>
  <span v-if="displaySuggestedValue" v-bind="$attrs">{{ getSuggestedValue }}</span>
</template>


<script setup>
import { computed, toRefs } from "vue";
import { useStore } from 'vuex';

defineOptions({
  inheritAttrs: false,
})

const props = defineProps({
  name: {
    type: String,
    required: true
  },
})

const store = useStore();
const { name } = toRefs(props)

const getSuggestedValue = computed(() => store.getters.getSuggestedValues(name.value));

const displaySuggestedValue = computed(() => getSuggestedValue.value !== null);
</script>
