<template>
  <ComponentWrapper :name="name" :classes="inputGroupClass" :id="id">
    <div v-if="prepend" class="input-block__prepend" :id="id + '__prepend'">
      <span v-html="prepend"></span>
    </div>

    <textarea
        v-bind="$attrs"
        :class="[inputClass, prepend ? 'input--has-prepend' : '', append ? 'input--has-append' : '']"
        :id="id"
        :value="inputValue"
        @input="inputValue=$event.target.value"
        @focus="inputTouched=true"
        :name="name"
    />

    <div v-if="append" class="input-block__append" :id="id + '__append'">
      <span v-html="append"></span>
    </div>

    <ErrorsPlaceholder v-if='displayError' :names=[name]></ErrorsPlaceholder>
  </ComponentWrapper>
</template>


<script setup>
import { computed, toRefs, watch } from "vue";
import { useStore } from "vuex";
import ComponentWrapper from "./component_wrapper.vue";
import ErrorsPlaceholder from "./errors_placeholder.vue";

defineOptions({
  inheritAttrs: false,
})

const props = defineProps({
  append: {
    type: String,
  },
  id: {
    type: String,
    required: true
  },
  name: {
    type: String,
    required: true
  },
  prepend: {
    type: String,
  },
  suggest_value: { // do not use directly (instead use the computed property suggestValue)
    type: [String, Boolean],
    required: false,
    default: false
  },
  display_error: { // do not use directly (instead use the computed property displayError)
    type: [String, Boolean],
    required: false,
    default: true
  }
})

const store = useStore();
const { append, name, id, prepend, suggest_value, display_error } = toRefs(props)

const displayValidationError = computed(() => {
  return inputTouched.value && inputError.value;
})

const displayValidationWarning = computed(() => {
  return inputTouched.value && !inputError.value && inputWarning.value
})

const suggestValue = computed(() => {
  return (suggest_value.value === 'true' || suggest_value.value === true || suggest_value.value === 'force')
})

const displayError = computed(() => {
  return (display_error.value === 'true' || display_error.value === true)
})

const useSuggestedValue = computed(() => {
  return (suggestValue.value && !inputTouched.value) || suggest_value.value === 'force'
})

const suggestedValue = computed(() => {
  return store.getters.getSuggestedValues(name.value)
})

const inputClass = computed(() => {
  return {
    "input-block__field": true,
    "input-block__field--invalid": (inputTouched.value && inputError.value),
    "input-block__field--warning": (inputTouched.value && !inputError.value && inputWarning.value)
  }
})

const inputError = computed(() => {
  if (inputTouched.value) {
    return store.getters.getError(name.value)
  } else {
    return null
  }
})

const inputWarning = computed(() => {
  if (inputTouched.value) {
    return store.getters.getWarning(name.value)
  } else {
    return null
  }
})

const inputGroupClass = computed(() => {
  return {
    "input-block": true,
  }
})

const inputTouched = computed({
  get () {
    return store.getters.getTouched(name.value);
  },
  set (value) {
    store.commit('setTouched', {
      value: value,
      name: name.value
    });
  }
});

const inputValue = computed({
  get () {
    return store.getters.getValue(name.value);
  },
  set (value) {
    store.dispatch('update', {
      value: value,
      name: name.value
    });
  }
});


watch(suggestedValue, (value, _oldValue) => {
  if (useSuggestedValue.value) {
    inputValue.value = value;
  }
});
</script>
