<template>
  <input v-bind="$attrs" :id="id" :name="name" :value="inputValue" @input="inputValue=$event.target.value" />
</template>


<script setup>
import { computed, toRefs, watch } from "vue";
import { useStore } from "vuex";

defineOptions({
  inheritAttrs: false,
})
const props = defineProps({
  append: {
    type: String,
  },
  name: {
    type: String,
    required: true
  },
  id: {
    type: String,
    required: true
  },
  prepend: {
    type: String,
  },
  suggest_value: { // do not use directly (instead use the computed property suggestValue)
    type: [String, Boolean],
    required: false,
    default: false
  },
})
const store = useStore();
const { append, name, id, prepend, suggest_value } = toRefs(props)
const inputValue = computed({
  get () {
    return store.getters.getValue(name.value);
  },
  set (value) {
    store.dispatch('update', {
      value: value,
      name: name.value
    });
  }
});
const suggestValue = computed(() => {
  return (suggest_value.value === 'true' || suggest_value.value === true)
})
const useSuggestedValue = computed(() => {
  return suggestValue.value
})
const suggestedValue = computed(() => {
  return store.getters.getSuggestedValues(name.value)
})
watch(suggestedValue, (value, _oldValue) => {
  if (useSuggestedValue.value) {
    inputValue.value = value;
  }
});
</script>
