<template>
  <ComponentWrapper :name="name" :classes="inputGroupClass" :id="id">

    <div v-if="prepend" class="input-block__prepend" :id="id + '__prepend'">
      <span v-html="prepend"></span>
    </div>

    <input type="hidden" :name="name" :value="inputValue"/>

    <flat-pickr
        ref="input"
        v-bind="$attrs"
        v-model="inputFormattedValue"
        :id="id"
        :class="[
            inputClass,
            prepend ? 'input--has-prepend' : '',
             append ? 'input--has-append' : '',
             ]"
        :config="config"
        @on-open="onFocusDatePicker"
    ></flat-pickr>

    <div v-if="append" class="input-block__append" :id="id + '__append'">
      <span v-html="append"></span>
    </div>

    <ErrorsPlaceholder v-if='displayError' :names=[name]></ErrorsPlaceholder>
  </ComponentWrapper>
</template>


<script>
import { debounce } from "lodash";
import { formatISO, isValid, parse, isMatch } from "date-fns";
import { format, utcToZonedTime, zonedTimeToUtc } from "date-fns-tz";
import flatPickr from "vue-flatpickr-component";
import "flatpickr/dist/flatpickr.css";
import * as R from 'ramda'
import ErrorsPlaceholder from "./errors_placeholder.vue";
import ComponentWrapper from "./component_wrapper.vue";

export default {
  data () {
    return {
      config: {
        enableTime: true,
        noCalendar: true,
        dateFormat: this.pickerFormat(),
        allowInput: true,
        time_24hr: true,
      },
      debouncedSetFormattedValue: debounce(this.setFormattedValue, 500),
    };
  },
  components: {
    ComponentWrapper,
    ErrorsPlaceholder,
    flatPickr,
  },
  inheritAttrs: false,
  props: {
    append: {
      type: String,
    },
    name: {
      type: String,
      require: true,
    },
    id: {
      type: String,
      require: true,
    },
    prepend: {
      type: String,
    },
    suggest_value: { // do not use directly (instead use the computed property suggestValue)
      type: [String, Boolean],
      require: false,
      default: false
    },
    display_error: { // do not use directly (instead use the computed property displayError)
      type: [String, Boolean],
      require: false,
      default: true
    },
    display_format: {
      type: String,
      require: false,
      default: "HH:mm",
    },
  },
  computed: {
    displayValidationError: function () {
      return this.inputTouched && this.inputError;
    },
    displayValidationWarning: function () {
      return this.inputTouched && !this.inputError && this.inputWarning;
    },
    suggestValue: function () {
      return (this.$props.suggest_value === 'true' || this.$props.suggest_value === true || this.$props.suggest_value === 'force')
    },
    displayError: function () {
      return (this.$props.display_error === 'true' || this.$props.display_error === true)
    },
    useSuggestedValue: function () {
      return (this.suggestValue && !this.inputTouched) || this.$props.suggest_value === 'force'
    },
    suggestedValue: function () {
      return this.$store.getters.getSuggestedValues(this.$props.name);
    },
    inputClass: function () {
      return {
        "input-block__field": true,
        "input-block__field--invalid": (this.inputTouched && this.inputError),
        "input-block__field--warning": (this.inputTouched && !this.inputError && this.inputWarning),
        datepicker: true,
      };
    },
    inputError: function () {
      if (this.inputTouched) {
        return this.$store.getters.getError(this.$props.name);
      } else {
        return null;
      }
    },
    inputWarning: function () {
      if (this.inputTouched) {
        return this.$store.getters.getWarning(this.$props.name);
      } else {
        return null;
      }
    },
    inputGroupClass: function () {
      return {
        "input-block": true,
      };
    },
    inputTouched: {
      get () {
        return this.$store.getters.getTouched(this.$props.name);
      },
      set (value) {
        this.$store.commit("setTouched", {
          value: value,
          name: this.$props.name,
        });
      },
    },
    inputValue: {
      get () {
        return this.$store.getters.getValue(this.$props.name);
      },
      set (value) {
        this.$store.dispatch("update", {
          value: value,
          name: this.$props.name,
        });
      },
    },
    inputFormattedValue: {
      get () {
        //update
        let value = this.$store.getters.getValue(this.$props.name)
        let datetime = value == null ? "" : new Date(value);

        if (isValid(datetime)) {
          return format(
              utcToZonedTime(datetime, 'UTC'),
              this.$props.display_format,
              { timeZone: "UTC" }
          );
        } else {
          return "";
        }
      },
      set (value) {
        // user is probably typing a new date, wait for him to finish
        // and avoid unnecessary updates of the state
        this.debouncedSetFormattedValue(value)
      },
    },
  },
  watch: {
    suggestedValue: function (value, _oldValue) {
      if (this.useSuggestedValue) {
        this.inputValue = value;
      }
    },
  },
  methods: {
    onFocusDatePicker: function () {
      this.inputTouched = true;
    },
    checkIsValidFormat: (value, display_format) => {
      return !R.isNil(value) && !R.isEmpty(value) && isMatch(value, display_format)
    },
    pickerFormat: function () {
      let format;

      if (this.$props.display_format === 'HH:mm') {
        format = 'H:i'
      }

      return format
    },
    setFormattedValue: function (value) {
      let display_format = this.$props.display_format

      if (!R.isNil(value) && !R.isEmpty(value) && !isMatch(value, display_format)) {
        // ignore non-blank & unmatched values
        // user is probably typing a new date, wait for him to finish
        return
      }


      let time = parse(value, display_format, new Date());
      let normalised_date = isValid(time) ? formatISO(zonedTimeToUtc(time, 'UTC')) : ''

      this.$store.dispatch("update", {
        value: normalised_date,
        name: this.$props.name,
      });
    }
  },
};
</script>
