<template>
  <ComponentWrapper :name="name" :classes="inputGroupClass" :id="id">

    <div v-for="value in choicesToJson" class="multi_check__flex-container">
      <div class="multi_check__checkbox-container">
        <input v-on:focus="inputTouched = true"
               type="checkbox"
               :value="value.id"
               :name="inputName"
               :id="`id-${value.id}`"
               @change="updateSelection"
               :checked="inputValue.includes(value.id)"
               class="multi_check__checkbox-input">
      </div>
      <div class="multi_check__label-container">
        <label class="multi_check__label-text" :for="`id-${value.id}`">{{ value.display_name }}</label>
      </div>
    </div>

    <input type="hidden" :id="id" :name="inputName" value=""/>

    <ErrorsPlaceholder v-if='displayError' :names=[name]></ErrorsPlaceholder>
  </ComponentWrapper>
</template>

<script setup>
import { computed, toRefs, watch } from 'vue';
import { useStore } from 'vuex';

import ErrorsPlaceholder from "./errors_placeholder.vue";
import ComponentWrapper from "./component_wrapper.vue";

const props = defineProps({
  name: {
    type: String,
    required: true
  },
  id: {
    type: String,
    required: true
  },
  choices: {
    type: String,
    required: true
  },
  suggest_value: { // do not use directly (instead use the computed property suggestValue)
    type: [String, Boolean],
    required: false,
    default: false
  },
  display_error: { // do not use directly (instead use the computed property displayError)
    type: [String, Boolean],
    required: false,
    default: true
  },
});

const store = useStore();
const { name, id, choices, suggest_value, display_error } = toRefs(props);

const choicesToJson = computed(() => {
  return store.getters.getPotentialValues(name.value) || JSON.parse(choices.value)
});

const displayValidationError = computed(() => {
  return inputTouched.value && inputError.value;
});

const displayValidationWarning = computed(() => {
  return inputTouched.value && !inputError.value && inputWarning.value;
});

const suggestValue = computed(() => {
  return (suggest_value.value === 'true' || suggest_value.value === true || suggest_value.value === 'force')
});

const displayError = computed(() => {
  return (display_error.value === 'true' || display_error.value === true)
});

const useSuggestedValue = computed(() => {
  return (suggestValue.value && !inputTouched.value) || suggest_value.value === 'force'
});

const suggestedValue = computed(() => {
  return store.getters.getSuggestedValues(name.value);
});

const inputError = computed(() => {
  if (inputTouched.value) {
    return store.getters.getError(name.value);
  } else {
    return null;
  }
});

const inputWarning = computed(() => {
  if (inputTouched.value) {
    return store.getters.getWarning(name.value);
  } else {
    return null;
  }
});

const inputGroupClass = computed(() => {
  return {
    "multi_check__wrapper": true
  }
});

const inputName = computed(() => {
  return (name.value + '[]')
});

const inputTouched = computed({
  get () {
    return store.getters.getTouched(name.value);
  },
  set (value) {
    store.commit('setTouched', {
      value: value,
      name: name.value
    });
  }
});

const inputValue = computed({
  get () {
    return store.getters.getValue(name.value);
  },
  set (value) {
    store.dispatch('update', {
      value: value,
      name: name.value
    });
  }
});

watch(suggestedValue, (value, _oldValue) => {
  if (useSuggestedValue.value) {
    inputValue.value = value;
  }
});

const updateSelection = (event) => {
  // important => do not update directly `values`, instead create a new object,
  // otherwise the vuejs reactivity might not work as expected
  const values = inputValue.value || []

  const value = event.target.value;
  const index = values.indexOf(value);

  if (event.target.checked) {
    if (index === -1) {
      inputValue.value = [...values, value]
    }
  } else {
    if (index !== -1) {
      inputValue.value = values.toSpliced(index, 1);
    }
  }
}

</script>
